import React from 'react';
import Genre from './Genre';
import Cart from './Cart';
import {useState} from 'react'

function Sidebar(props) {
  const [sidebar_contact, setSidebar_contact] = useState(false)
  return (
    <div className="sidebar">
        <Cart cartBook={props} />
        <Genre idGenreBooks={props.idGenreBooks} activeGenre={props.activeGenre} />
        <div className="sidebar__contact">
          <h3 onClick={() => setSidebar_contact(!sidebar_contact)} className="sidebar__contact-text">Контакты:</h3>
            {sidebar_contact && 
              <div className="sidebar__contact-items">
                <h3 className="sidebar__contact-phone">Телефон: (888)88888888</h3>
                <h3 className="sidebar__contact-email">Почта: zgs@gmail.com</h3>
              </div> 
            }
        </div>
    </div>
  );
}

export default Sidebar;