import React from 'react';
import logo from './search.png'

class HeaderSearch extends React.Component {
  
  render() {
    return (<form className="header-search">
        <input type="txt" placeholder="Enter Keywords"  className="header-search__input" />
        <div className="header-search__logo">
          <input type="image" name="picture" alt="поиск" src={logo} />
        </div>
      </form>
    )
  }
}
export default HeaderSearch;